	// import { apiOrderList } from '@/service/index'
	export default {
		data() {
			return {
				tabIndex: 0,
				tabList: ['全部','待支付','待发货','待收货', '售后'],
				status: 0,
				orderList: [],
				total: 0,
				currentPage: 1,
				pageSize: 20
			}
		},
		onLoad(options) {
      // this.tabIndex = options.status
      // this.getOrderList(1)
		},
		onReachBottom() {
      if(this.orderList.length >= this.total){
        return
      }
      this.currentPage++
      this.getOrderList(2)
    },
		methods: {
			selectTab(index) {
        this.tabIndex = index
        this.getOrderList(1)
			},
			getOrderList(type) {
				let params = {
					page: this.currentPage,
					size: this.pageSize
				}
				const status = this.tabIndex - 1
				if (status >= 0) {
          params.status = status
				}else {
					params.status = ''
				}
				apiOrderList(params)
				.then((res) => {
					if(res.code == 0) {
						this.total = res.data.total
						if(type == 1) {
              this.orderList = res.data.data
						}else {
              this.orderList = this.orderList.concat(res.data.data)
						}
            console.log(this.orderList)
            this.orderList.forEach((item) => {
            	let totalNum = 0
            	item.goods_cart.forEach((content) => {
            		totalNum = totalNum + content.buy_number
            	})
            	item.totalNum = totalNum
            })
					}
				})
			},
			shopDetailPage (item) {
				if (item.status !== 0) {
					return
				}
				let shopList = []
				item.goods_cart.forEach((item) => {
				  const shopItem = {
				    goods_id: item.goods_id,
				    goods_price: item.goods_price,
				    goods_name: item.goods_name,
				    goods_cover_img: item.goods_cover_img,
				    shopNum: item.buy_number,
				    cart_id: item.cart_id,
				    is_cash_back: item.is_cash_back || '0'
				  }
				  shopList.push(shopItem)
				})
				uni.setStorageSync('shopList', shopList)
				uni.navigateTo({
				  url: '/pages/confirm-order/confirm-order'
				});
			}
		}
	}
